<template>
  <div class="wrap-g" style="min-height: 100vh;">
    <mt-header title="修改支付密码" style="background: #fff;color: #1B1B1B;">
      <router-link to="/set" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <!--<mt-button icon="more" slot="right"></mt-button>-->
    </mt-header>
    <div style=";padding: 10px 20px;">请验证您的手机并设置支付密码</div>
    <div class="form" style="background: #fff;">
      <div class="list" style="margin-top: 0">
        <div class="item" style="position: relative">
          <input type="text" placeholder="请输入您的手机号" style="width: calc(100% - 115px)">
          <el-button plain style="position: absolute;padding: 7px 15px;right: 5px;top: 8px;color:#AAAAAA;">获取验证码</el-button>
        </div>
        <div class="item">
          <input type="number" placeholder="请输入验证码">
        </div>
        <div class="item">
          <input type="number" placeholder="请输入新的密码">
        </div>
        <div class="item">
          <input type="number" placeholder="请再次输入新密码">
        </div>
      </div>




    </div>
    <div style=";padding: 10px 20px">密码设置为6位数字</div>
    <div style="margin: 20px 0;padding: 0 15px;">
      <el-button type="primary" round style="width: 100%;margin: 15px 0;border-radius: 5px"  :disabled="radio==null">确定</el-button>
      <div style="margin-top: 30px;color: RGBA(68, 138, 202, 1);text-align: center">收不到验证码？</div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        radio: null,
      };
    },
    mounted(){

    },
    methods: {

    }
  }

</script>



<style lang="less" scoped>
  .form{
    border-top: 1px solid #f2f2f2;
    .list{
      padding: 0 15px;
      margin-top: 30px;
      .item{
        border-bottom: 1px solid #f2f2f2;
        input{
          outline: none;
          border: 0;
          width: 100%;
          height: 35px;
          padding: 5px 0 5px 5px;
        }
      }
    }
  }
  .bottom{

    position: absolute;bottom: 0;
    width: 100%;
    text-align: center;
    color: #959595;
    font-size: 12px;
    .title{
      position: relative;
      .txt{
        line-height: 30px;
      }
      .txt:before{
        content: '';
        position: absolute;;left: 0px;
        top:15px;
        width: calc(50% - 50px);
        height: 1px;
        background: #959595;
      }
      .txt:after{
        content: '';
        position: absolute;;right: 0px;
        top:15px;
        width: calc(50% - 50px);
        height: 1px;
        background: #959595;
      }
    }
  }
</style>




